'use strict';
import ALERTY     from '../alerty/alerty.js';

export default function flogs(data) {
    if (!(this instanceof flogs)) return new flogs(data);
    let DATA = $.extend(true,{}, data);
    let DefaultLog = {
        is_JS:true,
        framework:"Nyumphea v."+window.NYMPHEA_ver,
        ui_ver: window.UIversion,
        project_ver: window.PROJECTversion,
        browser: Meia.panda.Browser.getInfo().browser,
        browserInfo:Meia.panda.Browser.getInfo(),
        locationHref:window.location.href,
        user_name:window.user.name,
    };

    function titleCheck(title){
        let list = window.titleList
        if(_.isEmpty(list)) return true
        if(_.indexOf(list, title) >=0){
            return true
        }
        return false
    };
    function logCompile(log){
        log = $.extend(true,DefaultLog, log);
        return JSON.stringify(log, null, 4)
    }
    function toSendDataCompile(){
        let sendData = {
            project:window.project_name,
            title:DATA.title,
            uid:window.user.id,
            log:logCompile(DATA.log)
        }
        return sendData
    }
    function sender(sendData){
        let url;
        if(window.isDEVELOP){
            url = "https://dev-flogs2.dev.crazypanda.ru/addlog";
        }else{
            url = "https://flogs2.crazypanda.ru/addlog";
        }
        let query = $.ajax({
            url: url,
            type: 'POST',
            crossDomain: true,
            contentType:'application/x-www-form-urlencoded; charset=utf-8',
            data: sendData
        });
        return query
    }
    this.set = function(data){
        DATA =  $.extend(true,DATA, data);
    }
    this.send = function(){
        let toSendData = toSendDataCompile()
        if(!toSendData.log){
            ALERTY.error("FLOGS SENDER: log is empty");
            return
        }
        if(!toSendData.project){
            ALERTY.error("FLOGS SENDER: project is empty");
            return
        }
        if(!titleCheck(toSendData.title)){
            ALERTY.error("FLOGS SENDER: title notype");
            return
        }
        sender(toSendData);
    }
}
