'use strict';

export default function UrlQuery() {
  if (!(this instanceof UrlQuery)) return new UrlQuery();

  this.objToStr = function (data) { //преобразует объект в строку запроса

    let process;
    function addEl(el) {
      if (!process) {
        process = el
      } else {
        process += "&" + el
      }
    }
    _.each(data, (node, key) => {
      if (_.isArray(node)) {
        _.each(node, nodeAr => {
          let ar = "" + encodeURI(key) + "=" + encodeURI(nodeAr);
          addEl(ar)
        })

      } else {
        let el = "" + encodeURI(key) + "=" + encodeURI(node);
        addEl(el)
      }
    })
    return process
  };
  this.strToObj = function (queryString) {  //преобразует строку запроса в объект
    var query = {};
    var pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
    for (var i = 0; i < pairs.length; i++) {
      var seperatorIndex = pairs[i].indexOf('=');
      var key = pairs[i].slice(0, seperatorIndex)
      var value = pairs[i].slice(seperatorIndex + 1)
      query[decodeURIComponent(key)] = decodeURIComponent(value || '');
    }
    return query;
  }
}

