//facade for meia and panda
(function(root, factory) {
    if(typeof exports === 'object' && typeof module === 'object') 
        module.exports = factory();
    else if(typeof define === 'function' && define.amd) //by wp module
        define([], factory);
    else if(typeof exports === 'object')
        exports["meia"] = factory();
    else
        root["meia"] = factory();
    if (typeof meia === 'undefined' && !window.meia ) {
        window.meia = factory();
        var meia =  window.meia; 
    }

}(window, function() {
    var Meia = {};
    var Config = {
        useMeia:        true, //использовать meia.js
        usePanda:       false, //использовать panda.js
        usePandaPerfMonitor: false, //использовать отслеживание fps в panda.js
        isDev:          false,  //это dev инстанс
        useCustUrl:     false   //использовать кастомный url для загрузки подмодулей, используется если подключение не из ядра 
    };

    var OnLoad = {
        cb:undefined,
        loaded:false,
        meiaLoaded:false,
        meiaOnload:undefined,
        pandaLoaded:false,
        pandaOnload:undefined
    }
    var Const = {
        moduleUrl: '/m/js/core' //url для загрузки подмодулей  если подключение не ядра 
    }
    var defaultConsoleLogFNC = console.log

    Meia.setConfig = function(config){ //смотри Config
        if(typeof config.useMeia === 'boolean'){
            Config.useMeia = config.useMeia;
        }
        if(typeof config.usePanda === 'boolean'){
            Config.usePanda = config.usePanda;
            if(typeof config.usePandaPerfMonitor === 'boolean'){
                Config.usePandaPerfMonitor = config.usePandaPerfMonitor;
            }
        }
        if(typeof config.isDev === 'boolean'){
            Config.isDev = config.isDev;
        }
        if(typeof config.useCustUrl != 'boolean'){
            Config.useCustUrl = config.useCustUrl
        }
    };
    Meia.init = function(){
        console.log("Meia.init",Config)
        if(Config.useMeia){
            importMeia();
        }
        if(Config.usePanda){
            importPanda();
        }
    };
    Meia.onLoad = function(cb){
        console.log("Meia.onLoad call",OnLoad.loaded,window.onload)
        if(OnLoad.loaded){
            cb()
        }else{
            console.log("before window.onload call",window.onload) 
            if(window.onload){
                window.onload = function(){
                    console.log("window.onload call")
                    OnLoad.cb(cb) 
                }
            }else{
                OnLoad.cb(cb) 
            }

            
        }
    };


    OnLoad.cb = function(cb){
        console.log("OnLoad.cb")
        function allLoaded(){
            console.log("OnLoad.meiaLoaded == Config.useMeia",OnLoad.meiaLoaded, Config.useMeia, OnLoad.meiaLoaded == Config.useMeia)
            console.log("OnLoad.pandaLoaded == Config.usePanda",OnLoad.pandaLoaded, Config.usePanda, OnLoad.pandaLoaded == Config.usePanda)
            if(OnLoad.meiaLoaded == Config.useMeia 
                && OnLoad.pandaLoaded == Config.usePanda ){
                    OnLoad.loaded = true;
                    console.log("loaded")
                    cb()
                }
        }
        if(Config.useMeia){
            var appendLib = function(){
                Meia = extend(Meia, window.meia)
                window.meia = Meia;
                OnLoad.meiaLoaded = true;
                allLoaded()
            }
            if(typeof OnLoad.meiaOnload.onload === "function"){
                OnLoad.meiaOnload.onload = function(){
                    appendLib()
                }
            }else if(typeof window.meia !== 'undefined'){
                appendLib()
            }
        }
        if(Config.usePanda){
            console.log("Config.usePanda")
            var appendLib = function(){
                console.log("appendLib")
                panda.Initializer(Config.usePandaPerfMonitor)
                Meia.panda = panda;
                window.panda = panda;
                OnLoad.pandaLoaded = true;
                setPandaFacade()
                allLoaded()
            }
            if(typeof panda !== 'undefined'){
                appendLib()
            } else if(typeof OnLoad.pandaOnload.onload === "function"){
                OnLoad.pandaOnload.onload = function(){
                    console.log("pandaOnload")
                    appendLib()
                }
            } else {
                OnLoad.pandaOnload.addEventListener("load",function(){
                    console.log("addEventListener load")
                    appendLib()
                })
            }
        }
    }
    function importPanda(){
        var script = document.createElement('script');
        if(Config.useCustUrl){
            script.src = Config.useCustUrl+'/panda.js'
        }else{
            script.src = Const.moduleUrl+'/panda.js'
        }
        document.getElementsByTagName('head')[0].appendChild(script);
        OnLoad.pandaOnload = script;
    };
    function importMeia(){
        var script = document.createElement('script');
        if(Config.useCustUrl){
            script.src = Config.useCustUrl+'/meia.js'
        }else{
            script.src = Const.moduleUrl+'/meia.js'
        }
        document.getElementsByTagName('head')[0].appendChild(script);
        OnLoad.meiaOnload = script
    };
    

    function statSender(){
        if(Config.isDev == true){
            var statUrl = new Meia.panda.Url();
            statUrl.hostname = 'stat.dev.crazypanda.ru';
            statUrl.pathname = 'event';
            Meia.panda.Stat.intance = new Meia.panda.Stat(statUrl, 100);
        }
        this.event = function(project, site, type){
            /*чтобы добавить дополнительные uniq используй  statEvent.addUniq(uniqName, value, type)*/
            var statEvent = new Meia.panda.StatEvent(project,site,type);
            return statEvent 
        }
        this.send = function(statEvent){
            Meia.panda.Stat.intance.send(statEvent)
        }
        this.onSended =function(){
            return Meia.panda.Stat.intance.onSended
        }
        this.getInstance = function(){
            return Meia.panda.Stat.intance
        }
    }


    var extend = function ( defaults, options ) {
        var extended = {};
        var prop;
        for (prop in defaults) {
            if (Object.prototype.hasOwnProperty.call(defaults, prop)) {
                extended[prop] = defaults[prop];
            }
        }
        for (prop in options) {
            if (Object.prototype.hasOwnProperty.call(options, prop)) {
                extended[prop] = options[prop];
            }
        }
        return extended;
    };

    function setPandaFacade(){
        Meia.panda.statSender =  new statSender()
        Meia.const = {
            defaultConsoleLogFNC:defaultConsoleLogFNC //use me by use default console.log
        }
    }
    
   
    return Meia
}));   