'use strict';

import Loader from './Loader.js';

export let LoaderFactory = {
	loaders : [],
	default_error  : null,
    expected_error : false,
	get : function(url, params, request_method, expected_error, content_type) {
		var loader = new Loader(url, params, request_method, this.default_error, expected_error, content_type);
		this.loaders.push(loader);
		return loader;
	},
	clear : function() {
		for (var i in this.loaders) {
			this.loaders[i].disable();
		}
		this.loaders = [];
	},
	setDefaultError : function(cb) {
		this.default_error = cb;
	},
	setExpectedError : function(Expected) {
		this.expected_error = Expected;
	}
};

