const NYMPHEA = new nymphea();

Meia.setConfig({
    useMeia:false,
    usePanda:true,
    usePandaPerfMonitor:false,
    isDev:window.isDEVELOP,
    useCustUrl:window.MeiaJSUrl,
})
Meia.init()
