'use strict';
//SELF.ETALON - хранит в себе полученные данные, не изменяется в процессе использования. может быть изменена только после сохранения
import {
    LoaderFactory
} from '../services/LoaderFactory.js';

export default class Model {
    constructor(props = {}) {
        if (props.parent) this.PARENT = props.parent;
        this.CTRL = props.ctrl || "";
        this.TARGET = props.target || ""; //используется в формировании url
        this.METHOD = props.method || "";
        this.useAlternativeRequestParams = false;
        this.useAlternativeRequestFunction = false; //функция кастомной подстановки и выбора метода ,должна возвращать объект с url и request_method, нужно для специфики request_method 
        this.useProcessResolve = ['create', 'read', 'update', 'delete'];
        this.useExpectedError = false
        this.useContentType = false
        this.MODEL = {};
        this.ETALON = {};
        this.requestRead;
        this.promiseRead;
        this.isNewID = "isNew";
        this.getEtalon = this.getEtalon.bind(this);
    }
    _isErrorText(text) {
        if (_.isObject(text)) {
            if (text.error) {
                text = text.error
                if (_.isObject(text)) {
                    text = JSON.stringify(text)
                }
            } else {
                text = JSON.stringify(text)
            }
        } else {
            try {
                let newtext = JSON.parse(text);
                text = newtext.error
                if (!text) text = newtext.message
                if (_.isObject(text)) {
                    text = JSON.stringify(text)
                }
                if (!text) text = newtext
            } catch (msg) {}
        }
        return text
    }
    setUseAlternativeRequestParams() {
        this.useAlternativeRequestParams = true; //используются не реквест методы а дополнительные контроллеры
    }
    setUseProcessResolve(arrRequestType) { //указывает типы запросов для которых используется toModelProcess, toEtalonProcess  or PARENT.delete на ответ от сервера, если не установлено, toModelProcess &  PARENT.delete используется для всех
        this.useProcessResolve = arrRequestType || [];
    }
    baseUrl() {
        return window.server_url || "/";
    }
    setCtrlCtrl(ctrl) { //используется в формировании url
        if (!ctrl) {
            this.CTRL = "";
        } else {
            this.CTRL = ctrl;
        }
    }
    setTargetCtrl(target) { //используется в формировании url
        if (!target) {
            this.TARGET = "";
        } else {
            this.TARGET = "/" + target;
        }
    }
    setMethodCtrl(method) { //используется в формировании url
        if (!method) {
            this.METHOD = "";
        } else {
            n
            this.METHOD = "/" + method;
        }
    }
    setParameterCtrl(parameter) { //должен быть строкой типо test=1
        if (!parameter) {
            this.PARAMETER = "";
        } else {
            this.PARAMETER = "?" + parameter;
        }
    }
    processParametr(data) { //собирает строку url запроса из объекта
        let process;

        function addEl(el) {
            if (!process) {
                process = el
            } else {
                process += "&" + el
            }
        }
        _.each(data, (node, key) => {
            if (_.isArray(node)) {
                _.each(node, nodeAr => {
                    let ar = "" + decodeURIComponent(key) + "=" + decodeURIComponent(nodeAr);
                    addEl(ar)
                })

            } else {
                let el = "" + decodeURIComponent(key) + "=" + decodeURIComponent(node);
                addEl(el)
            }
        })
        return process
    }
    defaultModel() {
        return {}
    }
    clear() {
        this.MODEL = $.extend(true, this.MODEL, this.defaultModel());
    }
    init(props = {}) {
        this.clear();
        if (props.defaultData) {
            this.toEtalonProcess(props.defaultData);
            this.MODEL = $.extend(true, this.MODEL, this.toModelProcess(props.defaultData));
        }
    }
    processLoaderFactory(url, data, request_method, useExpectedError = false, useContentType = false) {
        return LoaderFactory.get(url, data, request_method, useExpectedError, useContentType);
    }
    create(data) {
        let SELF = this;
        let request_method = "POST";
        let url = SELF.baseUrl() + SELF.CTRL;
        if (SELF.TARGET) url += SELF.TARGET;
        if (SELF.METHOD) url += SELF.METHOD;
        if (SELF.useAlternativeRequestParams) {
            url += "/create";
            request_method = "POST"
        }
        if (SELF.PARAMETER) url += SELF.PARAMETER;
        if (SELF.useAlternativeRequestFunction) {
            let alternativeRequestFunction = SELF.useAlternativeRequestFunction()
            url += alternativeRequestFunction.url
            request_method = alternativeRequestFunction.request_method
        }
        SELF.requestCreate = SELF.processLoaderFactory(url, SELF.toServerProcess(data) || {}, request_method);
        SELF.promiseCreate = new Promise(function (resolve, reject) {
            SELF.requestCreate
                .success(function (data) {
                    if (SELF.useProcessResolve.indexOf('create') + 1) {
                        SELF.toEtalonProcess(data);
                        SELF.toModelProcess(data);
                    }
                    resolve(data)
                })
                .error(function (msg, text) {
                    reject(SELF._isErrorText(text), msg, text)
                })

        });
        return SELF.promiseCreate
    }
    getPromiseCreate() {
        return this.promiseCreate
    }

    read(data) {
        let SELF = this;
        let request_method = "GET";
        let url = SELF.baseUrl() + SELF.CTRL;
        if (SELF.TARGET) url += SELF.TARGET;
        if (SELF.METHOD) url += SELF.METHOD;
        if (SELF.useAlternativeRequestParams) {
            url += "/read";
            request_method = "POST"
        }
        if (SELF.PARAMETER) url += SELF.PARAMETER;
        if (SELF.useAlternativeRequestFunction) {
            let alternativeRequestFunction = SELF.useAlternativeRequestFunction()
            url += alternativeRequestFunction.url
            request_method = alternativeRequestFunction.request_method
        }
        SELF.requestRead = SELF.processLoaderFactory(url, data || {}, request_method);
        SELF.promiseRead = new Promise(function (resolve, reject) {
            SELF.requestRead
                .success(function (data) {
                    SELF.toEtalonProcess(data);
                    if (SELF.useProcessResolve.indexOf('read') + 1) SELF.toModelProcess(data);
                    resolve(data)
                })
                .error(function (msg, text) {
                    reject(SELF._isErrorText(text), msg, text)
                })
        });
        return SELF.promiseRead
    }
    getPromiseRead() {
        return this.promiseRead
    }

    update(data) {
        let SELF = this;
        let request_method = "PUT";
        let url = SELF.baseUrl() + SELF.CTRL;
        if (SELF.TARGET) url += SELF.TARGET;
        if (SELF.METHOD) url += SELF.METHOD;
        if (SELF.useAlternativeRequestParams) {
            url += "/update";
            request_method = "POST"
        }
        if (SELF.PARAMETER) url += SELF.PARAMETER;
        if (SELF.useAlternativeRequestFunction) {
            let alternativeRequestFunction = SELF.useAlternativeRequestFunction()
            url += alternativeRequestFunction.url
            request_method = alternativeRequestFunction.request_method
        }
        SELF.requestUpdate = SELF.processLoaderFactory(url, SELF.toServerProcess(data) || {}, request_method);
        SELF.promiseUpdate = new Promise(function (resolve, reject) {
            SELF.requestUpdate
                .success(function (data) {
                    if (SELF.useProcessResolve.indexOf('update') + 1) {
                        SELF.toEtalonProcess(data);
                        SELF.toModelProcess(data);
                    }
                    resolve(data)
                })
                .error(function (msg, text) {
                    reject(SELF._isErrorText(text), msg, text)
                })
        })
        return SELF.promiseUpdate
    }
    getPromiseUpdate() {
        return this.promiseUpdate
    }

    delete(data) {
        let SELF = this;
        let request_method = "DELETE";
        let url = SELF.baseUrl() + SELF.CTRL;
        if (SELF.TARGET) url += SELF.TARGET;
        if (SELF.METHOD) url += SELF.METHOD;
        if (SELF.useAlternativeRequestParams) {
            url += "/delete";
            request_method = "POST"
        }
        if (SELF.PARAMETER) url += SELF.PARAMETER;

        if (SELF.useAlternativeRequestFunction) {
            let alternativeRequestFunction = SELF.useAlternativeRequestFunction()
            url += alternativeRequestFunction.url
            request_method = alternativeRequestFunction.request_method
        }
        SELF.requestDelete = SELF.processLoaderFactory(url, data || {}, request_method);
        SELF.promiseDelete = new Promise(function (resolve, reject) {
            SELF.requestDelete
                .success(function (request) {
                    if (SELF.useProcessResolve.indexOf('delete') + 1) SELF.PARENT.delete(data ? data.id : SELF.MODEL.id);
                    resolve()
                })
                .error(function (msg, text) {
                    reject(SELF._isErrorText(text), msg, text)
                })
        })
        return SELF.promiseDelete
    }
    getPromiseDelete() {
        return this.promiseDelete
    }
    toEtalonProcess(data) {
        let SELF = this;
        SELF.ETALON = $.extend(true, {}, this.toModelProcess(data));
    }
    toModelProcess() {}
    toServerProcess() {}

    set(data, key, noExt, target) {
        if (key || _.isNumber(key)) {
            if (target || _.isNumber(target)) {
                if (noExt) {
                    this.MODEL[key][target] = data;
                    return
                } else {
                    this.MODEL[key][target] = $.extend(true, this.MODEL[key][target], data);
                    return
                }
            }
            if (noExt) {
                this.MODEL[key] = data;
                return
            } else {
                this.MODEL[key] = $.extend(true, this.MODEL[key], data);
                return
            }
        } else {
            if (noExt) {
                this.MODEL = data;
                return;
            }
            this.MODEL = $.extend(true, this.MODEL, data);
            return;
        }

    }
    get() {
        return $.extend(true, {}, this.MODEL);
    }
    getEtalon() {
        return $.extend(true, {}, this.ETALON);
    }
    getJSON() {
        let mdlData = {};
        _.each(this.MODEL, function (node, key) {
            mdlData[key] = node.get()
        })
        return $.extend(true, {}, mdlData);
    }
    isNew() {
        let SELF = this;
        if (this.MODEL.isNew) return true
        return false
    }
    setNew() {}
    setDefaultError(cb) {
        LoaderFactory.setDefaultError(cb)
    }
}