'use strict';
import MODEL  from './model.class.js';
function supports_html5_storage() {
    try {
        return 'localStorage' in window && window['localStorage'] !== null;
    } catch (e) {
        return false;
    }
}
export default class LOCALSTORAGE extends MODEL {
    constructor(props = {}){
        super(props = {});
        if(!supports_html5_storage()){
            console.error("localStorage not supported")
        }
        this.KEY = props.key || "";
    }
    setKey(key){ //ключ объекта модели в сторадже
        if(!key){
            this.KEY = "";
        }else{
            this.KEY = key;
        }
    }
    create(){
        let SELF = this;
        let before = window['localStorage'].getItem(SELF.KEY);
        if(before){
            console.error("LocalStorage: Create error. "+SELF.KEY+" is find")
            return
        }
        SELF.promiseCreate  = new Promise(function(resolve,reject){
            if(_.isObject(data)) data = JSON.stringify(data);
            window['localStorage'].setItem(SELF.KEY,data);
            resolve(data)
        })
        return SELF.promiseCreate
    }
    getPromiseCreate(){
        return SELF.promiseCreate
    }
    read(){ //получает данные по ключу из стораджа
        let SELF = this;
        SELF.promiseRead = new Promise(function(resolve,reject){
            let data = window['localStorage'].getItem(SELF.KEY);
            try{
                SELF.toEtalonProcess(JSON.parse(data));
                SELF.toModelProcess(JSON.parse(data));
            }catch (e) {
                SELF.toEtalonProcess({});
                SELF.toModelProcess({});
            }
            resolve(data)
        })
        return SELF.promiseRead
    }
    getPromiseRead(){
        return this.promiseRead
    }
    update(data){  //складывает данные по ключу в сторадж, мерджит только объекты, если отправить массив, добавит ключем обекта
        let SELF = this;
        let before = window['localStorage'].getItem(SELF.KEY);
        let toUpdate;
        if(!before){
            console.error("LocalStorage: Update error. "+SELF.KEY+" not find")
            return
        } 
        try{
            before = JSON.parse(before);
        }catch (e) { //пишем все типы, чтобы видеть что и как именно поддерживается
            if(_.isArray(before))   before = before;
            if(_.isString(before))  before = before;
            if(_.isNumber(before))  before = before;
            if(_.isBoolean(before)) before = before;
            if(_.isDate(before))    before = before;
            if(_.isNull(before))    before = before;
            before = {}
        }

        if(_.isObject(before)){
            if(_.isObject(data)) {
                let extendData = $.extend(true,before, data);
                toUpdate = JSON.stringify(extendData);
            }else{
                console.error("LocalStorage: Update error. "+SELF.KEY+" fatal type, updete data must be object")
                return
            }
        }else{
            toUpdate = data
        }

        SELF.promiseUpdate  = new Promise(function(resolve,reject){
            window['localStorage'].setItem(SELF.KEY,toUpdate);
            resolve(toUpdate)
        })
        return SELF.promiseUpdate
    }
    getPromiseUpdate(){
        return this.promiseUpdate
    }
    delete(data){ //удалить все данные из ключа
        let SELF = this;
        SELF.promiseDelete  = new Promise(function(resolve,reject){
            window['localStorage'].removeItem(SELF.KEY);
            resolve()
        })
        return SELF.promiseDelete
    }
    getPromiseDelete(){
        return this.promiseDelete
    }
}
