'use strict';

export default function stat(data) { //eventType: string, uniqs:[{addUniq}] || может отсутствовать
    if (!(this instanceof stat)) return new stat(data);
    let event = Meia.panda.statSender.event(window.project_name, "", data.eventName);
    if(data.uniqs){
        _.each(data.uniqs,(node)=>{
            this.addUniq(node)
        })
    }
    this.addUniq = (uniqName,value,type) => { //uniqName: string, value: string | number, type: string
        event.addUniq(uniqName, value, type)
    }
    this.send = () => {
        Meia.panda.statSender.send(event)
    }
    this.onSended = Meia.panda.statSender.onSended
    this.getInstance = Meia.panda.statSender.getInstance
    this.getEvent =()=>{
        return event
    }
}
